@import url("https://fonts.googleapis.com/css2?family=Major+Mono+Display&family=Share+Tech+Mono&display=swap");
html {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden !important;
  background-color: #d3d2cd;
}
body header {
  width: 100%;
  display: flex;
  font-family: "Share Tech Mono", monospace;
  position: relative;
  background: #d3d2cd;
}
body header h1:nth-child(1) {
  justify-content: left;
  text-align: left;
  font-size: 20px;
  margin: 20px;
}
body header h1:nth-child(2) {
  position: absolute;
  justify-content: center;
  text-align: center;
  font-size: 20px;
  margin: 20px;
  left: 50%;
  transform: translate(-50%);
}
body header h1 > a {
  color: #000;
  text-decoration: none;
}
body header ul {
  display: flex;
  position: absolute;
  right: 0px;
  margin: 20px;
  gap: 30px;
}
body header ul li {
  list-style-type: none;
  font-size: 20px;
}
body header ul li a {
  color: #000 !important;
  text-decoration: none;
}
body header .bar {
  position: absolute;
  width: 1200px;
  height: 1px;
  border-radius: 1000px;
  background-color: #000;
  left: 50%;
  bottom: 0px;
  transform: translate(-50%);
}
body picture img {
  -o-object-fit: cover;
     object-fit: cover;
  height: 400px;
  width: 300px;
  position: relative;
  z-index: 0;
}
body main {
  flex: 1 !important;
  display: flex;
  justify-content: center;
  flex-direction: column;
  background: #d3d2cd;
  position: relative;
}
body main h2 {
  position: relative;
  text-align: center;
  font-family: "Share Tech Mono", monospace;
  z-index: 1;
}
body main h2 span {
  position: absolute;
  left: 50%;
  transform: translate(-50%);
  font-size: 150px;
}
body main h2 span:nth-child(1) {
  position: relative;
  left: 0%;
}
body main h2 span::after {
  content: "";
  display: flex;
  position: absolute;
  width: 1200px;
  height: 1px;
  border-radius: 1000px;
  background-color: #000;
  left: 50%;
  transform: translate(-50%);
}
body main picture > img {
  left: 50%;
  transform: translate(-50%, -10%);
}
body .About {
  flex: 1 !important;
  display: flex;
  justify-content: center;
  flex-direction: column;
  background: #d3d2cd;
  position: relative;
}
body .About h2 {
  position: relative;
  text-align: left;
  font-family: "Share Tech Mono", monospace;
  z-index: 1;
  font-size: 70px;
  left: 20px;
}
body .About picture > img {
  left: 0%;
  transform: translate(50%, -20%) !important;
}
body .About article {
  position: absolute;
  font-family: "Share Tech Mono", monospace;
  font-size: 25px;
  max-width: 700px;
  bottom: 100px;
  right: 50px;
}
body .Work {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 20px;
  grid-row-gap: 10px;
  position: relative;
  top: 20px;
}
body .Work .Wb1 {
  grid-column: 1;
  grid-row: 1;
}
body .Work .Wb2 {
  grid-column: 2;
  grid-row: 1;
}
body .Work .Wb3 {
  grid-column: 1;
  grid-row: 2;
}
body .Work .Wb4 {
  grid-column: 2;
  grid-row: 2;
}
body .Work picture > a > img {
  width: 100%;
}
body footer {
  display: flex;
  justify-content: center;
  margin-top: auto;
  background: #d3d2cd;
}
body footer i {
  color: black;
}
body footer p {
  font-family: "Share Tech Mono", monospace;
  position: relative;
  font-size: 20px;
  bottom: 5px;
  right: 35%;
  transform: translate(50%, -50%);
}
body footer p .fa-location-crosshairs {
  position: relative;
  font-size: 30px;
  top: 5px;
}
body footer ul {
  display: flex;
  position: relative;
  left: 40%;
  transform: translate(-50%, -50%);
  gap: 50px;
}
body footer ul li {
  list-style-type: none;
  font-size: 30px;
}
body .Loader {
  display: flex;
  min-height: 100vh;
  width: 100%;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-family: "Share Tech Mono", monospace;
}
body .Loader .text {
  font-size: 20px;
  letter-spacing: 2px;
}
body .Loader .loading {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
body .Loader .loading .line-box {
  padding: 2px;
  width: 40%;
  height: 20px;
  border: 2px solid #000;
  border-radius: 20px;
}
body .Loader .loading .line-box .line {
  height: 20px;
  border-radius: 20px;
  background: #000;
  -webkit-animation: loading 1s forwards cubic-bezier(0, 0, 0, 0);
          animation: loading 1s forwards cubic-bezier(0, 0, 0, 0);
}
body .O4O4O {
  display: flex;
  justify-content: center;
  text-align: center;
  font-size: 50px;
  font-family: "Major Mono", monospace;
}
@media only screen and (max-height: 900px) {
  body footer {
    display: flex;
    justify-content: center;
    margin-top: auto;
    background: #d3d2cd;
  }
  body footer i {
    color: black;
  }
  body footer p {
    font-family: "Share Tech Mono", monospace;
    position: relative;
    font-size: 20px;
    bottom: 5px;
  }
  body footer p .fa-location-crosshairs {
    position: relative;
    font-size: 30px;
    top: 5px;
  }
  body footer ul {
    display: flex;
    position: relative;
    gap: 20px;
  }
  body footer ul li {
    list-style-type: none;
    font-size: 30px;
  }
}

@-webkit-keyframes loading {
  from {
    width: 0%;
  }
  to {
    width: 100%;
  }
}

@keyframes loading {
  from {
    width: 0%;
  }
  to {
    width: 100%;
  }
}/*# sourceMappingURL=style.css.map */